@font-face {
  font-family: 'icomoon';
  src:  url('./assets/fonts/icomoon.eot');
  src:  url('./assets/fonts/icomoon.eot?thgcsn#iefix') format('embedded-opentype'),
    url('./assets/fonts/icomoon.ttf') format('truetype'),
    url('./assets/fonts/icomoon.woff?thgcsn') format('woff'),
    url('./assets/fonts/icomoon.svg?thgcsn#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
   // use !important to prevent issues with browser extensions that change fonts
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-down:before {
  content: "\e900";
}
.icon-help:before {
  content: "\e901";
}
.icon-user:before {
  content: "\e902";
}
